import React from "react"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { Link } from "gatsby"
import faEnvelope from "@fortawesome/fontawesome-free-solid/faEnvelope"
import {
  Box,
  Heading,
  Form,
  Icon,
  Tag,
  Button,
} from "react-bulma-components/full"

const NewsletterForm = () => (
  <Box>
    <Link to="/"><Heading>Ivo's sporadischer Spam</Heading></Link>
    <Heading subtitle size={6}>
      Feinster Ecotainment Newsletter, Alter...
    </Heading>
    <Form.Field kind="addons">
      <Form.Control iconLeft fullwidth>
        <Form.Input
          color="success"
          type="email"
          placeholder="Psst, trag Dich ein diggi..."
        />
        <Icon align="left">
          <FontAwesomeIcon align="left" icon={faEnvelope} />
        </Icon>
      </Form.Control>
      <Form.Control iconLeft>
        <Button color="success">Trag mich ein</Button>
      </Form.Control>
    </Form.Field>
    <Tag.Group>
      <Tag color="dark">Umwelt</Tag>
      <Tag color="dark">Wissenschaft</Tag>
      <Tag color="dark">Aktivisten</Tag>
      <Tag color="dark">Birding</Tag>
      <Tag color="dark">Subkultur</Tag>
      <Tag color="dark">Trivia</Tag>
      <Tag color="dark">Lulz</Tag>
    </Tag.Group>
  </Box>
)

export default NewsletterForm
