import React from "react"
import Layout from "../components/layout"
import NewsletterForm from "../components/newsletterForm"
import NewsletterBox from "../components/newsletterBox"
import SEO from "../components/seo"
import {
  Container,
  Heading,
} from "react-bulma-components/full"

export default ({ data }) => (
  <Layout>
    <SEO title="Aktuell" keywords={[`workshops`, `handmade`, `kurse`]} />
      <Container Fluid>
        <NewsletterForm />
          <Heading size={3} renderAs="h2">Aktuell</Heading>
            {data.allJsonJson.edges.map(({ node }, index) => (
              <NewsletterBox node={node}/>
            ))}
      </Container>
  </Layout>
)

export const query = graphql`
  query {
    allJsonJson(
    sort: {
      fields: title
      order: DESC
    }
    limit: 1
  ) {
      edges {
        node {
          published
          title
          tags
          newsletter
        }
      }
    }
  }
`
